import { readText } from '../../util'
import Vue from 'vue'
import CodeViewer from './CodeViewer'

/**
 * 
 * @param buffer 
 * @param target 
 */
export default async function renderText(buffer, target) {
  const text = await readText(buffer)
  return new Vue({
    render: (h) => h(CodeViewer, { props: { value: text } })
  }).$mount(target)
}
